// To clear things when we float the element inside nav and ul
.clear {
  *zoom: 1;

  &:after {
    clear: both;
  }

  &:before,
  &:after {
    content: " ";
    display: table;
  }
}

.site-header {
  //padding-bottom: 1%;
  padding: 2% 2% 1% 2%;
  background: $brand-color;
  display: inline-block;
  float: left;
  width: 100%;

  .separator {
    user-select: none;
    opacity: $navbar-separator-opacity;

    &:first-child {
      display: none;
    }
  }

  a {
    color: $header-link-color;
  }
  .avatar {
    height: 3em;
    width: 3em;
    float: left;
    margin-top: -13px;
    border-radius: 0.2em;
    margin-right: 0.5em;
    padding: 10px 10px;
  }

  .site-title {
    float: left;
    margin-left: 10px;
    margin-right: 10px;
    font-weight: bold;
    font-family: $font-family-logo;
    font-size: 1.1em;
  }

  nav {
    list-style: none;
    margin: 0;
    padding: 0;

    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      line-height: 1.5;
      float: right;
      text-align: right;
      display: block;
    }

    li {
      display: inline;
      float: left;
      margin-right: 0.4em;
    }

    a {
      &:hover {
        color: $link-color;
      }

      &#pull {
        float: right;
        i {
          margin-top: 15px;
          margin-right: 10px;
        }
      }
    }
  }
}

/* ---- Responsive ---- */

@media (min-width: $break) {
  a#pull {
    display: none;
  }
}

@media screen and (max-width: $break) {
  nav {
    height: auto;

    .separator {
      display: none !important;
    }

    ul {
      width: 100%;
      overflow: hidden;

      /* fade out, then shrink */
      transition: opacity .25s 0s, font-size .25s 0s;
      transition-delay: 0s;

      &.hide {
        font-size: 0;
        margin: 0;
        opacity: 0;
        padding: 0;
      }
    }

    li {
      width: 100%;
      text-align: center;
      font-size: 1em;
      position: relative;
      background-color: darken($background-color, 2%);

      &:nth-of-type(4n) {
        background-color: $background-color;
      }

      a {
        width: 100%;
        margin: 0;
        display: block;
      }
    }
  }
}
